<template>
    <div class="about">
    
                     <div class="d-sm-none">
        <AboutNavigatorLandscape v-if="orientation === 'landscape-primary'" class="navigation"/>
        <AboutNavigatorPortrait v-if="orientation === 'portrait-primary'" class="navigation"/>
    </div>
    <div class="d-none d-sm-block">
       <AboutNavigatorLandscape/>
    </div>
      
    </div>
</template>

<script>
import AboutNavigatorLandscape from "@/components/about/AboutNavigatorLandscape";
import AboutNavigatorPortrait from "@/components/about/AboutNavigatorPortrait";

export default {
    components: {
     AboutNavigatorLandscape,
     AboutNavigatorPortrait
    },

    mounted() {
    window.addEventListener(
      "orientationchange",
      this.handleOrientationChange
        );
    },

    data() {
        return {
            orientation: "portrait-primary"
        }
    },

  methods: {
    handleOrientationChange() {
      this.orientation = window.screen.orientation.type
    } 
  }
};
</script>
